.franchisesuccess-slider-container {
  width: 1280px;
  height: 480px;
  margin: auto;
  position: relative;
  overflow: hidden;
  padding: 58px;
}

.franchisesuccess-slider {
  width: 100%;
  height: 100%;
  position: relative;
  margin: auto;
  overflow: hidden;
}

.slick-list {
  overflow: hidden;
}

.slick-track {
  display: flex;
  transition: transform 0.5s ease-in-out;
}

.franchisesuccess-slider-item {
  display: flex !important;
  /* Ensure items are flex */
  flex-shrink: 0;
  width: 100%;
  height: 100%;
}

.franchisesuccess-image-container {
  width: 50%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.franchisesuccess-image-container img {
  width: 480px;
  height: 480px;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.franchisesuccess-image-container img:hover {
  transform: translateY(-10px);
}

.franchisesuccess-content-container {
  width: 528px;
  height: 348px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: var(--bg-color);
  box-sizing: border-box;
  margin-top: 61px;
}

.franchisesuccess-text-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: -76px;
}

.franchisesuccess-text-content h2 {
  margin-bottom: 10px;
  font-size: 30px;
  font-family: Lilita One;
  font-weight: 400;
  color: var(--primary-orange);
}

.franchisesuccess-text-content p {
  font-size: 24px;
  font-family: "Geologica";
  font-weight: 400;
  color: var(--highlight-color);
}

.franchisesuccess-text-content p span {
  line-height: 1.5;
  font-size: 24px;
  font-family: "Geologica";
  font-weight: 400;
  color: var(--primary-orange);
}

/* Custom styles for slick buttons */
.slick-prev,
.slick-next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-color: orange;
  color: transparent;
  border: none;
  width: 50px;
  height: 50px;
  cursor: pointer;
  border-radius: 50%;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.slick-prev::before,
.slick-next::before {
  position: absolute;
  top: 11px;
  right: 14px;
  font-size: 20px;
  color: white;
}

.slick-prev::before {
  content: '◀';
}

.slick-next::before {
  content: '▶';
}

.slick-prev,
.slick-next {
  color: transparent;
  font-size: 0;
}

.slick-prev {
  left: 10px;
}

.slick-next {
  right: 10px;
}

