@media (max-width: 450px) {
  .investment-requirements {
    padding: 2rem;
    grid-template-columns: 1fr;
    /* Single column layout */
  }

  .investment-heading {
    font-size: 18px;
  }

  .investment-subheading {
    font-size: 50px;
  }

  .investment-description {
    font-size: 18px;
    margin-top: 7px;
    margin-bottom: 1px;
  }

  .investment-column h4 {
    font-size: 18px;
    margin-left: 70px;
  }

  .investment-column li {
    margin-left: 10px;
  }

  .investment-details {
    grid-template-columns: 1fr;
    /* Single column layout */
    gap: 1px;
    margin: 0;
  }

  .investment-column {
    width: 100%;
    margin: 0;
  }
}

@media (max-width: 377px) {
  .investment-requirements {
    padding: 1rem;
  }

  .investment-heading {
    font-size: 16px;
  }

  .investment-subheading {
    font-size: 30px;
  }

  .investment-description {
    font-size: 16px;
    margin-top: 5px;
