  }

  .investment-column h4 {
    font-size: 18px;
    margin-left: 60px;
  }

  .investment-column li {
    margin-left: 5px;
  }

}

@media (max-width: 321px) {
  .investment-requirements {
    padding: 1rem;
    grid-template-columns: 1fr;
    /* Single column layout */
  }

  .investment-heading {
    font-size: 14px;
  }

  .investment-subheading {
    font-size: 33px;
  }

  .investment-description {
    font-size: 12px;
    margin-top: 5px;
    margin-bottom: 1px;
  }

  .investment-column h4 {
    font-size: 16px;
    margin-left: 40px;
  }

  .investment-column li {
    margin-left: 3px;
  }
}